/*-------------------------*/
/* table
---------------------------*/
.table-over-hidden{
  width: 100%;
  margin: auto;
  overflow: hidden;
  .table-responsive-scroll{
    @media screen and (max-width: $mq768) {
      display: block;
      width: 100%;
      overflow-x: auto;
      -webkit-overflow-scrolling: touch;
      -ms-overflow-style: -ms-autohiding-scrollbar;
    }
  }
}
.table{
  width: 100%;
  max-width: 110rem; 
  // min-width: 76.8rem;
  text-align: center;
  color:$black;;
  margin: auto;
  @media screen and (min-width: $mq1200) {
    // width: 90%;
    // font-size: 2.1rem ;
  }
  caption{
    color: $black;
  }
  //thead
  .table-primary{
    background-color: $primary;
    color: $white;
  }
  thead th,
  tbody th {
    padding: 0.6rem 0 ;
    text-align: center;
  }
  tbody th,
  tbody td {
    font-weight: 400;
    vertical-align: middle;
  }
  // 
  th,td{
    border-top: 0.5rem solid $white;
    border-right: 0.5rem solid $white;
  }
  th:last-child,
  td:last-child{
    border-right: 0rem solid $white;
  }
  // 
  tbody tr {
    background-color: darken($bg-sixthly , 1.5%);
  }
}
