/*-------------------------*/
/* moreActivity
---------------------------*/
.moreActivity{
  background-color: $bg-thirdly;
  // 
  .moreTxt{
    width: 100%;
    max-width: 100%;
    background-color: $info;
    padding: 1.5rem 0rem;
    @media screen and (min-width: $mq1200) {
      // padding: 4.5rem 0rem;
    }
    // &:after{
    //   content: "";
    //   display: block;
    //   width:100%;
    //   height: 0.2rem;
    //   background-color: $secondary;
    //   position: absolute;
    //   left: 0;
    //   bottom: 1rem;
    // }
    &__title{
      @include fontSize(1.6rem , 1.5);
      color: $white;
      @media screen and (min-width: $mq1200) {
        @include fontSize(1.8rem , 1.5);
      }
    }
    &__detail{
      color: $bg-fourthly;
    }
  }
  // 
  .moreActivity__link{
    display: block;
  }
  .moreActivity__img{
    border: 0.5rem solid $gray-500;
    transition: all 0.3s;
    margin: auto;
    &:hover{
      border: 0.5rem solid $primary;
    }
  }
}


